class MenuForms {
  constructor (src, alt, title, descr, price, parentSelector, ...classes) {
    this.src = src;
    this.alt = alt;
    this.title = title;
    this.descr = descr;
    this.price = price;
    this.classes = classes;
    this.parentSelector = document.querySelector(parentSelector);

  }
  render () {
    const divMenu = document.createElement('div');

    if (this.classes.length === 0){
      this.divMenu = 'menu__item';
      divMenu.classList.add(this.divMenu);
    } else {
      this.classes.forEach(className => divMenu.classList.add(className));
    }

    
    divMenu.innerHTML = `
          <img src=${this.src} alt=${this.alt}>
          <h3 class="menu__item-subtitle">${this.title}</h3>
          <div class="menu__item-descr">${this.descr}</div>
          <div class="menu__item-divider"></div>
          <div class="menu__item-price">
              <div class="menu__item-cost">Ð¦ÐµÐ½Ð°:</div>
              <div class="menu__item-total"><span>${this.price}</span> Ð³Ñ€Ð½/Ð´ÐµÐ½ÑŒ</div>
          </div>
`;
    this.parentSelector.append(divMenu)
  }
}

